import * as React from 'react';
import './BackspaceIcon.css';

class BackspaceIcon extends React.Component {
  public render() {
    return (
      <svg className="BackspaceIcon-Image" viewBox="0 0, 52 40">
        <polygon
          fill="none"
          strokeWidth="4"
          stroke="black"
          strokeLinejoin="round"
          points="4 20, 16 4, 48 4, 48 36, 16 36"
        />
        <line x1="24" y1="12" x2="40" y2="28" stroke="black" strokeWidth="4" strokeLinecap="round" />
        <line x1="24" y1="28" x2="40" y2="12" stroke="black" strokeWidth="4" strokeLinecap="round" />
        Sorry, your browser does not support inline SVG.
      </svg>
    );
  }
}

export default BackspaceIcon;



